import React from "react";
import Sidebar from "grommet/components/Sidebar";
import Heading from "grommet/components/Headline";
import Link from "gatsby-link";

import MainMenu from "./MainMenu.jsx";
import BottomFooter from "./BottomFooter.jsx";

export default class Aside extends React.PureComponent {
  
  constructor() {
    super();
    this.state = {
      headerColor: undefined
    }
  }
  
  componentDidMount() {
    let color = this.props.colorOnMount;
    this.setState({
      headerColor: color
    });
  }

  handleChangedColor(color) {
    this.setState({
      headerColor: color
    });
  }


  render() {
    return <Sidebar fixed={false} full={false} className="sidebar">
      <Link onClick={this.handleChangedColor.bind(this, this.props.colorsBG.home)} className="header-class" style={{ backgroundColor: this.state.headerColor }} to="/">
        <Heading>Tihomir Selak</Heading>
      </Link>
      <MainMenu colors={this.props.colorsBG} menuClicked={this.handleChangedColor.bind(this)} />
      <BottomFooter />
    </Sidebar>;
  }
}



