import React from "react";
import Link from "gatsby-link";
import Menu from "grommet/components/Menu";
import Box from "grommet/components/Box";

export default class MainMenu extends React.PureComponent {

  constructor() {
    super();
    this.handleMenuClick = this.handleMenuClick.bind(this);
  }

  handleMenuClick(color){
    this.props.menuClicked(color);
  }

  render() {
    
    return <Box flex="grow" justify="start">
      <Menu className="main-menu" primary={true}>
        <Link onClick={this.handleMenuClick.bind(this, this.props.colors.web)} activeClassName="menu-active" className="menu-link-web" to="/web-development">
          Web Development
          </Link>
        <Link onClick={this.handleMenuClick.bind(this, this.props.colors.video)} activeClassName="menu-active" className="menu-link-video" to="/video-production">
          Video Production
          </Link>
        <Link onClick={this.handleMenuClick.bind(this, this.props.colors.android)} activeClassName="menu-active" className="menu-link-android" to="/android">
          Android
          </Link>
      </Menu>
    </Box>;
  }
}



