import React from "react";
import PropTypes from "prop-types";

// Grommet
import App from "grommet/components/App";
import Split from "grommet/components/Split";
import Box from "grommet/components/Box";
import Aside from "./../components/Aside/Aside.jsx";

// Styles
import "../scss/main.scss";

export default class IndexLayout extends React.Component {
  constructor(){
    super();
    this.state = {
      colorsBG: {
        home:"#F1C40F",
        video: "#E74C3C",
        web: "#2980B9",
        android: "#16A085"
      },
      colorOnMount: undefined
    }
    this.setColorOfContent = this.setColorOfContent.bind(this);
  }


  componentWillMount() {
    let color = setColorOnMount(this.props.location.pathname, this.state.colorsBG);
    this.setState({ colorOnMount: color });
  }

  setColorOfContent(){
    return this.state.colorsBG;
  }

  render() {
    const setColorOfContent = this.setColorOfContent
    return (
      <App centered={false}>
        <Split
          separator={true}
          priority="left"
          showOnResponsive="both"
          flex="right"
          className="split"
        >
          <Aside colorsBG={this.state.colorsBG} colorOnMount={this.state.colorOnMount} />
          <Box justify="center" align="center" pad="medium" className="content">
            {this.props.children({ ...this.props, layout: false, setColorOfContent })}
          </Box>
        </Split>
      </App>
    );
  }
}

IndexLayout.propTypes = {
  children: PropTypes.func
};

const setColorOnMount = (location, colors) => {
  let color = "";
  switch (location) {
    case "/":
      color = colors.home;
      break;
    case "/video-production":
      color = colors.video;
      break;
    case "/web-development":
      color = colors.web;
      break;
    case "/android":
      color = colors.android;
      break;
    default:
      color = colors.home;
  }
  return color;
}




