import React from "react";
import Footer from "grommet/components/Footer";
import Link from "gatsby-link";

import fontawesome from '@fortawesome/fontawesome'
import FontAwesomeIcon from '@fortawesome/react-fontawesome'
import brands from '@fortawesome/fontawesome-free-brands'
import envelope from '@fortawesome/fontawesome-free-solid/faEnvelope'


export default class BottomFooter extends React.PureComponent {
  constructor(){
    super();

  }
  render() {
    return <Footer className="footer">
          <ul className="icons">
            <li>
              <a href="https://github.com/Tihi321" target="_blank">
              <FontAwesomeIcon icon={["fab", "github-square"]} />
              </a>
            </li>
            <li>
              <a href="https://www.linkedin.com/in/selaktihomir/" target="_blank">
              <FontAwesomeIcon icon={["fab", "linkedin"]} />
              </a>
            </li>
            <li>
              <a href="https://www.youtube.com/channel/UCGGywniaR7IGOagnwC5ZsPA" target="_blank">
              <FontAwesomeIcon icon={["fab", "youtube-square"]} />
              </a>
            </li>
            <li>
              <a href="https://play.google.com/store/apps/developer?id=Tihomir+Selak" target="_blank">
              <FontAwesomeIcon icon={["fab", "google-play"]} />
              </a>
            </li>
            <li>
              <a href="mailto:knox@dr.com" target="_blank">
              <FontAwesomeIcon icon={envelope} />
              </a>
            </li>
          </ul>
          <div className="footer-brand">
            Built with <a href="https://www.gatsbyjs.org/" target="_blank">
              GatsbyJS
            </a>
          </div>
      </Footer>;
  }
}



